import React, { useState, useEffect } from 'react'
import { financeInstitution, caseSourceAll, caseTypeAll, caseStatus, provinceAll } from '../../Utility/dataCase'
import uuid from "uuid"

const ModalAddCase = () => {
  const [newCase, setNewCase] = useState({})

  useEffect(() => {
  })

  const handleChange = (e) => {
    setNewCase({ ...newCase, [e.target.name]: e.target.value })
  }

  const resetForm = () => {
    setNewCase({})
  }

  return (
    <div>
      <div id="modalAddCase" className="modal modal-fixed-footer">
        <div className="modal-content">
          <h6>ADD NEW CASE  api</h6>
          <div className="row">

            <div className="col s12 m6 l2">
              <label >Case Status / </label>
              <select
                name="caseStatus"
                className='browser-default'
                value={ newCase.caseStatus || "DEFAULT" }
                onChange={ handleChange }
              >
                <option value="DEFAULT" disabled> </option>
                { caseStatus.map((c) => <option key={ uuid.v4() } value={ c }>{ c }</option>) }
              </select>
            </div>

            <div className=" col s12 m6 l2 ">
              <label >JOB No.</label>
              <input
                type="text"
                name="jobNum"
                value={ newCase.jobId }
                onChange={ handleChange }
                className="validate"

              />
            </div>

            <div className=" col s12 m6 l2 ">
              <label >Receiver Date/ </label>
              <input
                type="date"
                value={ newCase.date }
                name='dateAddCase'
                onChange={ handleChange }

              />
            </div>

            <div className="col s12 m6 l2 ">
              <label >Case Receiver/ </label>
              <input
                name='receiver'
                value={ newCase.receiver || '' }
                onChange={ handleChange }
                type="text"
                className="validate" />
            </div>

            <div className="col s12 m6 l2 ">
              <label >Case Type / </label>
              <select
                name='caseType'
                value={ newCase.caseType || 'DEFAULT' }
                onChange={ handleChange }
                className='browser-default'
              >
                <option value="DEFAULT" disabled> </option>
                { caseTypeAll.map((ct) => <option key={ uuid.v4() } value={ ct }>{ ct }</option>) }
              </select>
            </div>

            <div className="col s12 m6 l2 ">
              <label >Case Soure/</label>
              <select
                name='caseSource'
                value={ newCase.caseSource || 'DEFAULT' }
                onChange={ handleChange }
                className='browser-default'
              >
                <option value="DEFAULT" disabled>  </option>
                { caseSourceAll.map((cS) => <option key={ uuid.v4() } value={ cS }>{ cS }</option>) }
              </select>
            </div>
          </div>

          <h6>INFO. CASE</h6>
          <div className="row">

            <div className="row">
              <div className="col s12 m6 l2 ">
                <label>First Name</label>
                <input type="text" />
              </div>
              <div className="col s12 m6 l2 ">
                <label>Last Name</label>
                <input type="text" />
              </div>
              <div className="col s12 m6 l2 ">
                <label>Phone 1</label>
                <input type="text" />
              </div>
              <div className="col s12 m6 l2 ">
                <label>Phone 2</label>
                <input type="text" />
              </div>
            </div>

            <div className="row">
              <div className="col s12 m6 l2 ">
                <label>Licence Plate No. </label>
                <input type="text" />
              </div>
              <div className="col s12 m6 l2 ">
                <label >Province / </label>
                <select
                  name='province'
                  value={ newCase.province || "DEFAULT" }
                  className='browser-default'
                  onChange={ handleChange }
                >
                  <option value="DEFAULT" disabled> </option>
                  { provinceAll.map((pv) => <option key={ uuid.v4() } value={ pv }>{ pv }</option>) }
                </select>
              </div>
              <div className="col s12 m6 l2 ">
                <label>Bran / </label>
                <input type="text" />
              </div>
              <div className="col s12 m6 l2 ">
                <label>Model / </label>
                <input type="text" />
              </div>
              <div className="col s12 m6 l2 ">
                <label>Sub-model / </label>
                <input type="text" />
              </div>
              <div className="col s12 m6 l2 ">
                <label>Car Year / </label>
                <input type="text" />
              </div>
            </div>
            <div className="col s12 m6 l3 ">
              <label>Current Finance </label>
              <select
                name="oldFinance"
                value={ newCase.oldFinance || "DEFAULT" }
                className='browser-default'
                onChange={ handleChange }
              >
                <option value="DEFAULT" disabled> </option>
                { financeInstitution.map((ct) => <option key={ uuid.v4() } value={ ct }>{ ct }</option>) }

              </select>
            </div>
            <div className="col s12 m6 l3 ">
              <label>Finance Institution / </label>
              <select
                name="newFinance"
                value={ newCase.newFinance || "DEFAULT" }
                className='browser-default'
                onChange={ handleChange }
              >
                <option value="DEFAULT" disabled> </option>
                { financeInstitution.map((ct) => <option key={ uuid.v4() } value={ ct }>{ ct }</option>) }
              </select>
            </div>
            <div className="col s12 m6 l2 ">
              <label>Approved Amount /  </label>
              <input type="text" />
            </div>
            <div className="col s12 m6 l2 ">
              <label>Close Amount /  </label>
              <input type="text" />
            </div>
            <div className="col s12 m6 l2 ">
              <label>Down Payment / </label>
              <input type="text" />
            </div>

          </div>{/* finish row info */ }
        </div>{/* finish modal content */ }
        <div className="modal-footer ">
          <button className="waves-effect btn blue lighten left ">Save</button>
          <button className="waves-effect btn orange black-text left " onClick={ resetForm } style={ { marginLeft: '10px' } }>reset</button>
          <button className="modal-close waves-effect btn white black-text right">close</button>
        </div>
      </div>

    </div >
  )
}

export default ModalAddCase
