import React, { useEffect, useState } from 'react'
import Navbar from './../layout/Navbar';
import Search from './Search';
import ModalCreate from './ModalCreate';
import ModalRead from './ModalRead';
import ModalEdit from './ModalEdit';
import ModalDelete from './ModalDelete';
import ModalAddCaseCustomer from './ModalAddCaseCustomer';
import TableHead from './TableHead';
import axios from 'axios'
import uuid from "uuid"
import M from 'materialize-css/dist/js/materialize.min.js'
import url from '../../Utility/url'
// import fackeCustomer from '../../Utility/fackeCustomer'




const Customers = () => {

  const [customers, setCustomers] = useState([])
  const [customer, setCustomer] = useState({})

  useEffect(() => {
    getAllCustomers()
    M.Modal.init(document.querySelectorAll('.modal'), {});
  }, [])

  useEffect(() => {

  }, [customers])

  const getAllCustomers = () => {
    // setCustomers(fackeCustomer)

    axios.get(`${url}/customer_limit?size=50&page=2`)
      .then(res => { setCustomers(res.data.message) })
      .catch(err => { console.log(err) })
  }

  const searchCustomer = (text) => {
    axios.get(`${url}/search_customer?value=${text}&parameter=firstname`)
      .then(res => {
        console.log(res.data.message)
        setCustomers(res.data.message)
      })
      .catch(err => console.log(err))
  }

  const addCustomer = (c) => {
    axios.post(`${url}/add_customer`, c)
      .then(res => {
        M.toast({ html: `${res.data.message}` })
        getAllCustomers()
      })
      .catch(err => { console.log(err) })
  }

  const readCustermer = (c) => setCustomer(c)

  const deleteCustomer = (id) => {
    axios.delete(`${url}/delete_customer?customer_id=${id}`)
      .then(res => {
        M.toast({ html: `${res.data.message}` })
        getAllCustomers()
      })
  }

  const editCustomer = (id, newCustomer) => {

    axios.put(`${url}/edit_customer?customer_id=${id}`, newCustomer)
      .then(res => { getAllCustomers() })
      .catch(err => { console.log(err) })

  }



  return (
    <>
      <Navbar />
      <main>
        <div className="nContainer">

          <h3> All Customers : <span className="chip purple"> { customers.length } </span></h3>
          <Search searchCustomer={ searchCustomer } getAllCustomers={ getAllCustomers } />
          <a className="waves-effect waves-light btn modal-trigger" href="#modalCreate" style={ { margin: '25px' } }>create customer</a>

          <ul class="pagination">
            <li class="disabled"><a href="#!"><i class="material-icons">chevron_left</i></a></li>
            <li class="active"><a href="#!">1</a></li>
            <li class="waves-effect"><a href="#!">2</a></li>
            <li class="waves-effect"><a href="#!">3</a></li>
            <li class="waves-effect"><a href="#!">4</a></li>
            <li class="waves-effect"><a href="#!">5</a></li>
            <li class="waves-effect"><a href="#!"><i class="material-icons">chevron_right</i></a></li>
          </ul>

          <table className="centered responsive-table">
            <TableHead />
            <tbody>
              { customers.map(cust => (
                <tr key={ cust.customer_id ? cust.customer_id : uuid.v4() }>
                  <td>{ cust.id }</td>
                  <td>{ cust.firstname }</td>
                  <td>{ cust.lastname }</td>
                  <td>{ cust.tel }</td>
                  <td>{ cust.line }</td>
                  <td>{ cust.email }</td>
                  <td style={ { width: "20%" } }>{ cust.address }</td>
                  <td>{ cust.date_create }</td>
                  <td>{ cust.date_update }</td>
                  <td>
                    <a className="waves-effect  btn modal-trigger m-1 white blue-text" href="#modalAddCaseCustomer" onClick={ () => readCustermer(cust) }>Add Case</a>
                    <a className="waves-effect blue  btn modal-trigger m-1" href="#modalRead" onClick={ () => readCustermer(cust) }>read</a>
                    <a className="waves-effect yellow black-text btn modal-trigger m-1" href="#modalEdit" onClick={ () => readCustermer(cust) }>edit</a>
                    <a className="waves-effect pink btn modal-trigger m-1" href="#modalDelete" onClick={ () => readCustermer(cust) }>delete</a>
                  </td>
                </tr>
              )) }
            </tbody>
          </table>

          <ModalAddCaseCustomer customer={ customer } />
          <ModalCreate addCustomer={ addCustomer } />
          <ModalRead customer={ customer } />
          <ModalEdit customer={ customer } editCustomer={ editCustomer } />
          <ModalDelete customer={ customer } deleteCustomer={ deleteCustomer } />
        </div>
      </main>

    </>
  )
}

export default Customers
