const statusNumCase = (type) => {
  switch (type) {
    case 'receive': return [1, 'Receive Date / 	']
    case 'contact_customer': return [2, 'Contact Customer Date / 	']
    case 'account_closing': return [3, 'Account Closing Date / 	']
    case 'transfer_doc_received': return [4, 'Transfer Doc. Received / 	']
    case 'transfer_doc_submitted': return [5, 'Transfer Doc. Submitted / 	']
    case 'book_received': return [6, 'Book Received Date / 	']
    case 'submit_book_transfer': return [, 'Submit Book Transfer Date / ']
    case 'car_check_up': return [8, 'Car Check-Up / 	']
    case 'book_transfer': return [9, 'Book Transfer Date / 	']
    case 'book_copy_received': return [10, 'Book Copy Received / 	']
    case 'deposit_doc_to_new_bank': return [11, 'deposit_doc_to_new_bank / 	']
    case 'submit_book_deposit_return': return [12, 'Submit Book Deposit Return / ']
    case 'book_received_back': return [13, 'Book Received/ 	']
    case 'cash_received': return [14, 'Cash Received / 	']
    case 'book_deposit_received': return [15, 'Book Deposit Received/ ']
    case 'submit_book_to_new_finance': return [16, 'Submit Book to New Finance / ']
    default: return;
  }

}
const statusCase = (type) => {
  switch (type) {
    case 0: return 'Receive Date / 	'
    case 1: return 'Contact Customer Date / 	'
    case 2: return 'Account Closing Date / 	'
    case 3: return 'Transfer Doc. Received / 	'
    case 4: return 'Transfer Doc. Submitted / 	'
    case 5: return 'Book Received Date / 	'
    case 6: return 'Submit Book Transfer Date / '
    case 7: return 'Car Check-Up / 	'
    case 8: return 'Book Transfer Date / 	'
    case 9: return 'Book Copy Received / 	'
    case 10: return 'deposit_doc_to_new_bank / 	'
    case 11: return 'Submit Book Deposit Return / '
    case 12: return 'Book Received/ 	'
    case 13: return 'Cash Received / 	'
    case 14: return 'Book Deposit Received/ '
    case 15: return 'Submit Book to New Finance / '

    default: return;
  }

}

const statusTacking = (type) => {
  switch (type) {
    case 0: return 'receive'
    case 1: return 'contact_customer'
    case 2: return 'account_closing'
    case 3: return 'transfer_doc_received'
    case 4: return 'transfer_doc_submitted'
    case 5: return 'book_received'
    case 6: return 'submit_book_transfer'
    case 7: return 'car_check_up'
    case 8: return 'book_transfer'
    case 9: return 'book_copy_received'
    case 10: return 'deposit_doc_to_new_bank'
    case 11: return 'submit_book_deposit_return'
    case 12: return 'book_received_back'
    case 13: return 'cash_received'
    case 14: return 'book_deposit_received'
    case 15: return 'submit_book_to_new_finance'
    default: return;

  }
}

