const actCase = (step, oneCase) => {
  switch (step) {
    case 0:
      return ['1./ Receive Date / ', oneCase.receive_date, oneCase.receive_note, oneCase.receive_yn]
    case 1:
      return ['2. / Contact Customer Date ', oneCase.contact_customer_date, oneCase.contact_customer_note, oneCase.contact_customer_yn]
    case 2:
      return ['3. / Account Closing Date ', oneCase.account_closing_date, oneCase.account_closing_note, oneCase.account_closing_yn]
    case 3:
      return ['4. / Transfer Doc. Received / 	', oneCase.transfer_doc_received_date, oneCase.transfer_doc_received_note, oneCase.transfer_doc_received_yn]
    case 4:
      return ['5. / Transfer Doc. Submitted / 	', oneCase.transfer_doc_submitted_date, oneCase.transfer_doc_submitted_note, oneCase.transfer_doc_submitted_yn]
    case 5:
      return ['6. / Book Received Date / 	', oneCase.submit_book_transfer_date, oneCase.submit_book_transfer_note, oneCase.submit_book_transfer_yn]
    case 6:
      return ['7. / Submit Book Transfer Date / ', oneCase.submit_book_transfer_date, oneCase.submit_book_transfer_note, oneCase.submit_book_transfer_yn]
    case 7:
      return ['8. / Car Check-Up / 	', oneCase.car_check_up_date, oneCase.car_check_up_note, oneCase.car_check_up_yn]
    case 8:
      return ['9. / Book Transfer Date / 	', oneCase.book_transfer_date, oneCase.book_transfer_note, oneCase.book_transfer_yn]
    case 9:
      return ['10. / Book Copy Received / 	', oneCase.book_copy_received_date, oneCase.book_copy_received_note, oneCase.book_copy_received_yn]
    case 10:
      return ['11. / deposit_doc_to_new_bank / 	', oneCase.deposit_doc_to_new_bank_date, oneCase.deposit_doc_to_new_bank_note, oneCase.deposit_doc_to_new_bank_yn]
    case 11:
      return ['12. / Submit Book Deposit Return / ', oneCase.submit_book_deposit_return_date, oneCase.submit_book_deposit_return_note, oneCase.submit_book_deposit_return_yn]
    case 12:
      return ['13. / Book Received/ 	', oneCase.book_received_back_date, oneCase.book_received_back_note, oneCase.book_received_back_yn]
    case 13:
      return ['14. / Cash Received / 	', oneCase.cash_received_date, oneCase.cash_received_note, oneCase.cash_received_yn]
    case 14:
      return ['15. / Book Deposit Received/ ', oneCase.book_deposit_received_date, oneCase.book_deposit_received_note, oneCase.book_deposit_received_yn]
    case 15:
      return ['16. / Submit Book to New Finance / ', oneCase.submit_book_to_new_financ_date, oneCase.submit_book_to_new_financ_note, oneCase.submit_book_to_new_financ_yn]
    default:
      return;
  }

}

