import React, { useState, useEffect } from 'react'
import Navbar from './../layout/Navbar';
// import ModalView1 from './ModalView1';
import ModalTracking from './ModalTracking';
import ModalAddCase from './ModalAddCase';
import ModalDeleteCase from './ModalDeleteCase'
import TheadCase from './TheadCase';
import url from '../../Utility/url'
import axios from 'axios';
import uuid from "uuid"
import M from 'materialize-css/dist/js/materialize.min.js'

const useId = 'mohexc'



const Cases = () => {
  const [cases, setCases] = useState([])
  const [singleCase, setSingleCase] = useState([])
  const [stateSearch, setStateSearch] = useState({
    starDate: '',
    endDate: ""
  })

  const [textSearch, setTextSearch] = useState(``)

  // page
  const [currPage, setCurrPage] = useState(1)
  const [limitPage, setLimitPage] = useState(30)
  const [totalPage, setTotalPage] = useState(30)

  useEffect(() => {
    M.Dropdown.init(document.querySelectorAll('.dropdown-trigger'), {
      coverTrigger: false,
      autoTrigger: true,
      constrainWidth: true
    });
    M.Modal.init(document.querySelectorAll('.modal'), {})
    M.Modal.init(document.querySelectorAll('#ModalDeleteCase'), {})
    M.FormSelect.init(document.querySelectorAll('select'), {});
    //   M.Datepicker.init(document.querySelectorAll('#startDate'), {});
    //   M.Datepicker.init(document.querySelectorAll('#endDate'), {});
  }, [])

  useEffect(() => {
    getAllCase()
  }, [])

  const getAllCase = () => {


    axios.get(`${url}/case_limit?size=${50}&page=${1}`)
      .then(res => {
        setCases(res.data.message)

      })
      .catch(err => console.log(err))
  }

  const getCaseLimit = () => {
    axios.get(`${url}/case_limit?size=${limitPage}&page=${currPage}`)

  }

  const deleteCase = (caseId) => {
    axios.delete(`${url}/delete_case?case_id=${caseId}`)
      .then(res => {
        M.toast({ html: `${res.data.message}` })
        getAllCase()
      })
      .then(err => console.log(err))
  }

  const handleSingleCase = (c) => setSingleCase(c)

  const prevPage = () => {
    setCurrPage(currPage - 1)

  }

  const nextPage = () => {
    setCurrPage(currPage + 1)
  }


  return (
    <>
      <Navbar />
      <main>
        <div className="nContainer">
          <h3>Cases All : <span className="chip  orange">{ cases.length }</span></h3>
          <div className="row">

            <div className="input-field col s6 m6 l2">
              <p>
                <label>
                  <input
                    type="checkbox"

                  /><span>job_id</span>
                </label>
              </p>
            </div>

            <div className="input-field col s6 m6 l2">
              <p>
                <label>
                  <input
                    type="checkbox"

                  /><span>name</span>
                </label>
              </p>
            </div>


            <div className="input-field col s12 m12 l4">
              <select >
                <option >CASE Status</option>
                <option value="1"></option>
                <option value="2"></option>
                <option value="3"></option>
                <option value="4"></option>
                <option value="5"></option>
                <option value="6"></option>
                <option value="7"></option>
                <option value="8"></option>
                <option value="9"></option>
                <option value="10">		</option>
                <option value="11"></option>
                <option value="12"></option>
                <option value="13"></option>
                <option value="14"></option>
                <option value="15"></option>
              </select>
            </div>
            <div className="input-field col s12 m6 l2 ">
              <i className="material-icons prefix">date_range</i>
              <input
                type="date"

                id='startDate'
                name="startDate"
              ></input>
              <label htmlFor="">Start date</label>
            </div>
            <div className="input-field col s12 m6 l2">
              <i className="material-icons prefix">date_range</i>
              <input
                type="date"
                id='endDate'
                name="endDate"
              ></input>
              <label htmlFor="">End date</label>
            </div>
          </div>
          {/* SEARCH */ }
          <div className="row">
            <div className="input-field col s12 m6">
              <i className="material-icons prefix">textsms</i>
              <input
                type="text"
                className="autocomplete"
                id="autocomplete-input"
                name=''

              />
              <label htmlFor="icon_prefix2">Search</label>
            </div>
            <button className="btn waves-effect waves-light ">search</button>
            <button className="btn pink waves-effect waves-light right">clear search</button>
          </div>

          <div className="row">
            <a className='btn green  waves-effect waves-light right' href='http://192.168.1.126:5009/api/v1/case_excel' target='blank' download>dewoload excel</a>
            <div>
              <ul className="pagination">
                <li className={ currPage <= 1 ? `disabled` : `waves-effect` }><a href="#!"><i className="material-icons" onClick={ prevPage }>chevron_left</i></a></li>
                <li className="active waves-effect"><a href="#!">1</a></li>
                <li className="waves-effect"><a href="#!">2</a></li>
                <li className="waves-effect"><a href="#!">3</a></li>
                <li className="waves-effect"><a href="#!">4</a></li>
                <li className="waves-effect"><a href="#!">5</a></li>
                <li className={ currPage >= totalPage ? `disabled` : `waves-effect` }><a href="#!"><i className="material-icons" onClick={ nextPage }>chevron_right</i></a></li>
              </ul>
            </div>
          </div>
          {/* TABALE */ }
          <div className="row">
            <table className="responsive-table">
              <TheadCase />
              <tbody>
                { cases.map(c => (
                  <tr key={ uuid.v4() }>
                    <td >{ c.case_id }</td>
                    <td >{ c.date_update }</td>
                    <td>{ c.status }</td>
                    <td >{ c.case_source }</td>
                    <td >{ c.name }</td>
                    <td>{ c.job_id }</td>
                    <td>{ c.car_license }</td>
                    <td >{ c.new_bank }</td>

                    <td className="" >
                      <a className="waves-effect waves-light blue btn modal-trigger m-1" href="#modalTracking" onClick={ () => handleSingleCase(c) }>Track</a>

                      <a className="waves-effect waves-light pink btn modal-trigger btnModalView" href="#modalDeleteCase" onClick={ () => handleSingleCase(c) }>delete</a>
                    </td>
                  </tr>
                )) }
              </tbody>
            </table>
          </div>

          <ModalTracking singleCase={ singleCase } />
          <ModalAddCase />
          <ModalDeleteCase singleCase={ singleCase } deleteCase={ deleteCase } />
          {/* 
          <div className="fixed-action-btn">
            <button className="btn-floating btn-large waves-effect modal-trigger" href="#modalAddCase">
              <i className="large material-icons">add</i>
            </button>
          </div> */}


        </div>
      </main>
    </>
  )
}

export default Cases
