import React, { useEffect, useState } from 'react'
import { Steps } from 'rsuite';
import { statusTacking } from '../../Utility/statusCase'
import { actCase, provinceAll, conditionCheckCar, conditionKeppDocument, staffOperOartrust, marginAccount } from '../../Utility/dataCase'
import 'rsuite/dist/styles/rsuite-default.css'
import M from 'materialize-css/dist/js/materialize.min.js'
import axios from 'axios';
import uuid from 'uuid';
import url from '../../Utility/url'
import pdfMake from 'pdfmake/build/pdfmake'
import pdfFonts from 'pdfmake/build/vfs_fonts'
import moment from 'moment'
import cartrustLogo from '../../img/cartrustLogo.png'

pdfMake.vfs = pdfFonts.pdfMake.vfs

pdfMake.fonts = {
  THSarabunNew: {
    normal: 'THSarabunNew.ttf',
    bold: 'THSarabunNew-Bold.ttf',
    italics: 'THSarabunNew-Italic.ttf',
    bolditalics: 'THSarabunNew-BoldItalic.ttf'
  },
  Roboto: {
    normal: 'Roboto-Regular.ttf',
    bold: 'Roboto-Medium.ttf',
    italics: 'Roboto-Italic.ttf',
    bolditalics: 'Roboto-MediumItalic.ttf'
  }
}


const initialValueTracking = {
  tracking: "",
  note_status: "",
  date: "",
  user_id: "",
  F2_picture: "",
  take_car_picture: "",
  approve_amount: "",
  job_id: "",
  old_finance_closing_fee: "",
  old_finance_transfer_fee: "",
  book_closing_fee: "",
  vat7_fee: "",
  transfer_fee: "",
  duty_fee: "",
  discount_fee: "",
  car_shield_fee: "",
  car_insurance_fee: "",
  transfer_service_fee: "",
  contract_fee: "",
  outside_transfer_fee: "",
  tax_renewal_fee: "",
  act_renewal_fee: "",
  yes_no: null,
  car_license_book_picture: "",
  close_amount: "",
  deposit: "",
  downPayment: "",
  hirePurchase: '',
  phoneHirePurchase: '',
  condiCheckCar: '',
  condiKeppDoc: "",
  addressCustomer: "",
  staffCartrust: "",
  accFreeMargin: "",
  refinanceF2: false,
  tradingThemselF2: false,
  pledgeF2: false,
  cameraF2: false,

}

const userId = "mohexc"

const ModalTracking = ({ singleCase }) => {


  // data
  const [caseId, setCaseId] = useState('')
  const [aCase, setACase] = useState({})
  const [valueTrack, setValueTrack] = useState(initialValueTracking)
  const [aCustomer, setACustomer] = useState({})
  const [page, setPage] = useState(1)


  // ui
  const [step, setStep] = useState(0)
  const [readCase, setReadCase] = useState([])
  const [prove, setProve] = useState('no')
  const [completTracking, setCompletTracking] = useState('')

  useEffect(() => {
    setStep(0)
    setCaseId(singleCase.case_id)
    setACase(singleCase)
    setReadCase(actCase(step, aCase))
    oneCustomer(singleCase.customer_id)

  }, [singleCase])

  useEffect(() => {

  }, [page])

  useEffect(() => {
    M.Modal.init(document.querySelectorAll('#modalF2'), {})
  }, [])

  useEffect(() => {
    refechData()
    setValueTrack(initialValueTracking)
    setValueTrack({ ...valueTrack, tracking: statusTacking(step) })
    setReadCase(actCase(step, aCase))

  }, [step])

  useEffect(() => {
    cartrustTotalCost()
  }, [valueTrack.old_finance_closing_fee, valueTrack.old_finance_transfer_fee, valueTrack.vat7_fee, valueTrack.transfer_fee, valueTrack.duty_fee, valueTrack.discount_fee])

  const oneCustomer = (customerId) => {
    axios.get(`${url}/customer?customer_id=${customerId}`)
      .then(res => {
        setACustomer(res.data.message)
        console.log(res.data.message)
      }).catch(err => { console.log(err) })
  }

  const refechData = () => {
    axios.get(`${url}/case?case_id=${caseId}`)
      .then(res => {
        setACase(res.data.message)
        setReadCase(actCase(step, aCase))
      })
      .catch(err => { console.log(err) })
  }

  const setValueProve = () => {
    if (prove === 'yes') { setProve('no') }
    if (prove === 'no') { setProve('yes') }
  }

  const nextStep = () => setStep(step + 1)

  const prevStep = () => setStep(step - 1)

  const handleChangeTracking = (e) => {
    setValueTrack({ ...valueTrack, [e.target.name]: e.target.value })

  }

  const beforSaveTracking = () => {
    setValueTrack({ ...valueTrack, user_id: userId })
    saveTracking(valueTrack)
  }
  const handleChangeCheack = (e) => {
    setValueTrack({ ...valueTrack, [e.target.name]: e.target.checked })
  }

  const handleChangefilePicture = (e) => {
    const file = e.target.files[0]
    const reader = new FileReader()
    reader.readAsDataURL(file)
    reader.onload = () => setValueTrack({ ...valueTrack, file: reader.result });
  }

  const cartrustTotalCost = () => {
    let arr = [parseInt(valueTrack.old_finance_closing_fee), parseInt(valueTrack.old_finance_transfer_fee), parseInt(valueTrack.vat7_fee), parseInt(valueTrack.transfer_fee), parseInt(valueTrack.duty_fee), parseInt(valueTrack.discount_fee)]
    let reducer = (acc, curr) => acc + curr
    let total = arr.reduce(reducer)
    setValueTrack({ ...valueTrack, cartrust_total_cost: total })

  }


  const printPDF = () => {
    var docDefinition = {
      content: [
        {
          columns: [{ width: 150, height: 60, image: cartrustLogo }, { text: ``, style: `header` },
          {
            fontSize: 12,
            text: `F2
             ${moment().format('l')}
            : 
            JOB Number: ${aCase.job_id}
                  `
          },
          ],
        },
        { text: ` ` },
        { columns: [{ width: 100, text: `  ${aCase.name}` }, { text: `` }, { text: `` }, { text: `` }] },
        { columns: [{ text: `  ${aCase.car_brand}` }, { text: ` ${aCase.car_license}` }, { text: `  ` }, { text: ` ${aCase.province} ` }, { text: `   cc` }] },
        {
          columns: [[
            { columns: [``, `jhjhkjhkjh`, ``, ``, `jhkjhk`] },
            { columns: [` `, `hjhkjhjkhkjh`, ``, ``, `jhkh`] },
            { columns: [`() `, `hjhkjhkj`] },
            {
              columns: [
                {
                  width: 50,
                  text: ` `
                },
                {
                  width: 60,
                  text: `\n / \n  \n `
                },
                {
                  table: {
                    heights: [10, 10, 10, 10],
                    body: [
                      ['24', '36', '48', '60', '72'],
                      [``, ``, ``, ``, ``],
                      [``, ``, ``, ``, ``],
                      [``, ``, ``, ``, ``]

                    ]
                  }
                }
              ]
            }
          ], {
            width: 150,
            columns: [[
              ``,
              ``,
              `. Oper Cartrust`,
              ``,
              ``,
            ], [
              `${valueTrack.condiCheckCar}`,
              `${valueTrack.condiKeppDoc}`,
              `${valueTrack.staffCartrust}`,
              `${valueTrack.addressCustomer}`,
              `${valueTrack.accFreeMargin}`,
            ]],

          }]
        },
        `\n`,
        { columns: [`cartrust`, ``] },
        { columns: [{ width: 100, text: `1.` }, { width: 100, text: `${valueTrack.old_finance_closing_fee}` }, { width: 25, text: `` }, ``] },
        { columns: [{ width: 100, text: `2.` }, { width: 100, text: `${valueTrack.old_finance_transfer_fee}` }, { width: 25, text: `` }, ``] },
        { columns: [{ width: 100, text: `3.` }, { width: 100, text: `${valueTrack.book_closing_fee}` }, { width: 25, text: `` }, ``] },
        { columns: [{ width: 100, text: `4. 7%` }, { width: 100, text: `${valueTrack.vat7_fee}` }, { width: 25, text: `` }, ``] },
        { columns: [{ width: 100, text: `5.` }, { width: 100, text: `${valueTrack.transfer_fee}` }, { width: 25, text: `` }, ``] },
        { columns: [{ width: 100, text: `6.` }, { width: 100, text: `${valueTrack.duty_fee}` }, { width: 25, text: `` }, ``] },
        { columns: [{ width: 100, text: `` }, { width: 100, text: `${valueTrack.discount_fee}` }, { width: 25, text: `` }, ``] },
        { columns: [{ width: 100, text: `` }, { width: 100, text: `${valueTrack.cartrust_total_cost}` }, { width: 25, text: `` }, ``] },
        `\n`,
        { columns: [` `, ``] },

        { columns: [{ width: 100, text: `1.Car Shield` }, { width: 100, text: `${valueTrack.car_shield_fee}` }, { width: 25, text: `` }, ``] },
        { columns: [{ width: 100, text: `2.` }, { width: 100, text: `${valueTrack.car_insurance_fee}` }, { width: 25, text: `` }, ``] },
        { columns: [{ width: 100, text: `3.` }, { width: 100, text: `${valueTrack.transfer_service_fee}` }, { width: 25, text: `` }, ``] },
        { columns: [{ width: 100, text: `4.` }, { width: 100, text: `${valueTrack.contract_fee}` }, { width: 25, text: `` }, ``] },
        { columns: [{ width: 100, text: `5.` }, { width: 100, text: `${valueTrack.outside_transfer_fee}` }, { width: 25, text: `` }, ``] },
        { columns: [{ width: 100, text: `6.` }, { width: 100, text: `${valueTrack.tax_renewal_fee}` }, { width: 25, text: `` }, ``] },
        { columns: [{ width: 100, text: `7..` }, { width: 100, text: `${valueTrack.act_renewal_fee}` }, { width: 25, text: `` }, ``] },
        { columns: [{ width: 100, text: `` }, { width: 100, text: `` }, { width: 25, text: `` }, ``] },
        { columns: [{ width: 100, text: `` }, { width: 100, text: `` }, { width: 25, text: `` }, ``] },
        { text: `\n` },
        { columns: [{ width: 100, text: `1]	` }, ``, { alignment: 'right', text: `` }] },
        { columns: [{ width: 100, text: `2]	` }, ``, { alignment: 'right', text: `` }] },
        { columns: [{ width: 100, text: `3]	 ` }, ``, { alignment: 'right', text: `` }] },
        `\n`,
        { text: ` :   3       50%   5   ` },
        { fontSize: 16, text: ` A/C PAYEE ONLY ` },
        { alignment: 'center', text: `,  ` },
        { alignment: 'center', text: ` 02-276-5765 -  09.00-18.00 .` },
        `\n`,
        { text: `....................................................  	` },
        `\n`,
        { alignment: 'right', text: `.............................................................................................................................` }
      ],// end content 
      styles: {
        header: { fontSize: 18 },
        acPayeeOnly: { fontSize: 30 },
        rexText: {},
        blueText: {},
        boldText: {}
      },
      pageMargins: [40, 30, 40, 30],

      defaultStyle: {
        font: "THSarabunNew",
        fontSize: 11,
        columnGap: 20
      }
    };
    pdfMake.createPdf(docDefinition).download(`f2`)

  }

  const saveTracking = (body) => {

    if (valueTrack.date === "") {
      setCompletTracking('check date tracking')
    }
    if (step === 2) {
      console.log(`tracking case level 3`)
      axios.put(`${url}/tracking_case3?case_id=${caseId}`, body)
        .then(res => {
          M.toast({ html: `${res.data.message}` })
          console.log(res.data.message)
          setCompletTracking('save tracking case  3 ')
          // setCompletTracking('save tracking case  3 ')
        })
        .catch(err => { console.log(err) })

    }

    if (step === 8) {
      console.log(`tracking case level 9`)

      axios.put(`${url}/tracking_case9?case_id=${caseId}`, body)
        .then(res => {
          console.log(res.data.message)
          M.toast({ html: `${res.data.message}` })
          setCompletTracking('save tracking case  9 ')
        })
        .catch(err => { console.log(err) })
    }

    if (step === 10) {
      console.log(`tracking case level 11`)
      axios.put(`${url}/tracking_case10?case_id=${caseId}`, body)
        .then(res => {
          M.toast({ html: `${res.data.message}` })
          console.log(res.data.message)
          setCompletTracking('save tracking case  11 ')
        })
        .catch(err => { console.log(err) })
    }

    if (step === 14) {

      axios.put(`${url}/tracking_case15?case_id=${caseId}`, body)
        .then(res => {
          console.log(res.data)
          M.toast({ html: `${res.data.message}` })
          setCompletTracking('save tracking case  15 ')
        })
        .catch(err => { console.log(err) })

    }
    else {
      axios.put(`${url}/tracking_case?case_id=${caseId}`, body)
        .then(res => {
          console.log(res.data)
          M.toast({ html: `${res.data.message}` })
          setCompletTracking(`save tracking case  ${step + 1} `)
        })
        .catch(err => { console.log(err) })

    }
    setTimeout(() => { setCompletTracking('') }, 3000)
  }

  return (
    <div>
      <div id="modalTracking" className="modal modal-fixed-footer">
        <div className="modal-content">

          <div className="row">
            <div className="col s12">
              <div className='ballStep'>
                <Steps current={ step } >
                  {/* currentStatus="" */ }
                  <Steps.Item />
                  <Steps.Item />
                  <Steps.Item />
                  <Steps.Item />
                  <Steps.Item />
                  <Steps.Item />
                  <Steps.Item />
                  <Steps.Item />
                  <Steps.Item />
                  <Steps.Item />
                  <Steps.Item />
                  <Steps.Item />
                  <Steps.Item />
                  <Steps.Item />
                  <Steps.Item />
                  <Steps.Item />
                </Steps>
              </div>
            </div>
          </div>
          <h5 className="center-align">INFO CASE ID : <span className="chip blue white-text">{ aCase.case_id }</span></h5>

          <table className="centered" >
            <thead>
              <tr>
                <th>Team :</th>
                <th>Doc No. /  :</th>
                <th>Contract Officer /  :</th>
                <th>JOB No</th>
                <th>Customer Name / </th>
                <th>LP No. / </th>
                <th>Province / </th>
                <th>Case Status/</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td> { aCase.team } </td>
                <td> { aCase.document_id } </td>
                <td> { aCase.contract_officer } </td>
                <td> { aCase.job_id } </td>
                <td> { aCase.name } </td>
                <td> { aCase.car_license } </td>
                <td> { aCase.province } </td>
                <td> { aCase.status } </td>
              </tr>
            </tbody>
          </table>

          {/* Tracking System */ }

          <h5 className="center-align"><span className="green-text">Tracking</span></h5>

          { completTracking && (<h6 className="red-text center-align ">{ completTracking }</h6>) }

          <div className="row">
            <div className="col s12 m6 l3">
              Tracking Case
              <input
                type="text"
                name="tracking"
                value={ readCase[0] || "" }
                className="browser default"
                readOnly
              />
            </div>
            <div className="col s12 m6 l3">
              { readCase[1]
                ? <><span className="red-text" >Old Date : </span>{ readCase[1] }</>
                : 'Date Tracking' }
              <input
                type="date"
                id=""
                name="date"
                disabled={ step === 0 }
                value={ valueTrack.date || '' }
                onChange={ handleChangeTracking }
              />

            </div>
            <div className="col s12 m6 l4">
              { readCase[2]
                ? <> <span className="red-text" >Old Note : </span>{ readCase[2] }</>
                : `Note Tracking` }
              <input
                type="text"
                id=""
                name="note_status"
                disabled={ step === 0 }
                value={ valueTrack.note_status || '' }
                onChange={ handleChangeTracking }

              />
            </div>
            <div className="col s12 m6 l2">
               : { readCase[3] }
              <input
                className="btn pink col s12"
                type="button"
                id=""
                name="note_status"
                disabled={ step === 0 }
                value={ prove }
                onClick={ setValueProve }
              />
            </div>

          </div>

          {/* Document F2 */ }

          { step === 2 && (
            <>
              <h5 className="center-align pink-text "> <span className="chip red white-text">F2</span> </h5>
              <div className="row">
                <div className="col s12 m6 l2">
                   : { singleCase.name }
                </div>
                <div className="col s12 m6 l2">
                   :
                </div>
              </div>

              <div className="row">
                <div className="col s12 m6 l2">
                   : { singleCase.car_brand }
                </div>
                <div className="col s12 m6 l2">
                   : { singleCase.car_model }
                </div>

                <div className="col s12 m6 l2">
                   : { singleCase.car_license }
                </div>
                <div className="col s12 m6 l2">
                   :
                </div>
              </div>

              <div className="row">
                <div className="col s12 m6 l2">
                  
                  <select
                    className="browser-default"
                    name="condiCheckCar"
                    value={ valueTrack.condiCheckCar || 'DEFAULT' }
                    onChange={ handleChangeTracking }
                  >
                    <option value="DEFAULT" disabled> </option>
                    { conditionCheckCar.map(c => <option key={ uuid.v4() }>{ c }</option>) }
                  </select>
                </div>
                <div className="col s12 m6 l2">
                  
                  <select
                    className="browser-default"
                    name="condiKeppDoc"
                    value={ valueTrack.condiKeppDoc || 'DEFAULT' }
                    onChange={ handleChangeTracking }
                  >
                    <option value="DEFAULT" disabled> </option>
                    { conditionKeppDocument.map(c => <option key={ uuid.v4() }>{ c }</option>) }
                  </select>
                </div>
                <div className="col s12 m6 l2">
                  . oper Cartrst
                  <select
                    className="browser-default"
                    name="staffCartrust"
                    value={ valueTrack.staffCartrust || 'DEFAULT' }
                    onChange={ handleChangeTracking }
                  >
                    <option value="DEFAULT" disabled>. oper Cartrst </option>
                    { staffOperOartrust.map(c => <option key={ uuid.v4() }>{ c }</option>) }
                  </select>
                </div>
                <div className="col s12 m6 l2">
                  
                  <select
                    className="browser-default"
                    name="addressCustomer"
                    value={ valueTrack.addressCustomer || 'DEFALT' }
                    onChange={ handleChangeTracking }
                  >
                    <option value="DEFALT" disabled> </option>
                    { provinceAll.map(province => <option key={ uuid.v4() } value={ province }>{ province }</option>) }
                  </select>
                </div>
                <div className="col s12 m6 l2">
                  
                  <select
                    className="browser-default"
                    name="accFreeMargin"
                    value={ valueTrack.accFreeMargin || 'DEFALT' }
                    onChange={ handleChangeTracking }
                  >
                    <option value="DEFALT" disabled> </option>
                    { marginAccount.map(ma => <option key={ uuid.v4() } value={ ma }>{ ma }</option>) }
                  </select>
                </div>
              </div>

              <div className="row">
                <div className="col s12 m6 l2 mb-3" style={ { display: "flex" } }  >
                  <input
                    type="checkbox"
                    name="refinanceF2"
                    checked={ valueTrack.refinanceF2 }
                    onChange={ handleChangeCheack }
                  />
                  <span></span>
                  <input
                    type="checkbox"
                    name="tradingThemselF2"
                    checked={ valueTrack.tradingThemselF2 }
                    onChange={ handleChangeCheack }
                  />
                  <span></span>
                  <input
                    type="checkbox"
                    name="pledgeF2"
                    checked={ valueTrack.pledgeF2 }
                    onChange={ handleChangeCheack }
                  />
                  <span></span>
                  <input
                    type="checkbox"
                    name="cameraF2"
                    checked={ valueTrack.cameraF2 }
                    onChange={ handleChangeCheack }
                  />
                  <span></span>
                </div>
              </div>

              <div className="row">
                <div className="col s12 m12 l4  blue lighten-5">
                  <h6> CarTrust</h6>
                  <div className="col s6">
                    **
                    <input
                      type="number"
                      min="0"
                      name="old_finance_closing_fee"
                      value={ valueTrack.old_finance_closing_fee }
                      onChange={ handleChangeTracking }
                    />
                  </div>
                  <div className="col s6">
                    
                    <input type="text" />
                  </div>

                  <div className="col s6">
                    **
                    <input
                      type="number"
                      min="0"
                      name="old_finance_transfer_fee"
                      value={ valueTrack.old_finance_transfer_fee }
                      onChange={ handleChangeTracking }
                    />
                  </div>
                  <div className="col s6">
                    
                <input type="text" />
                  </div>

                  <div className="col s6">
                    
                    <input
                      type="number"
                      min="0"
                      name="book_closing_fee"
                      value={ valueTrack.book_closing_fee }
                      onChange={ handleChangeTracking }
                    />
                  </div>
                  <div className="col s6">
                    
                <input type="text" />
                  </div>

                  <div className="col s6">
                     7%**
                <input
                      type="number"
                      min="0"
                      name="vat7_fee"
                      value={ valueTrack.vat7_fee }
                      onChange={ handleChangeTracking }

                    />
                  </div>
                  <div className="col s6">
                    
                <input
                      type="text"
                    />
                  </div>

                  <div className="col s6">
                    **
                <input
                      type="number"
                      min="0"
                      name="transfer_fee"
                      value={ valueTrack.transfer_fee }
                      onChange={ handleChangeTracking }
                    />
                  </div>
                  <div className="col s6">
                    
                <input
                      type="text"
                    />
                  </div>

                  <div className="col s6">
                    
                <input
                      type="number"
                      min="0"
                      name="duty_fee"
                      value={ valueTrack.duty_fee }
                      onChange={ handleChangeTracking }
                    />
                  </div>
                  <div className="col s6">
                    
                <input
                      type="text"
                    />
                  </div>

                  <div className="col s6">
                    **
                <input
                      type="number"
                      min="0"
                      name="discount_fee"
                      value={ valueTrack.discount_fee }
                      onChange={ handleChangeTracking }

                    />
                  </div>
                  <div className="col s6">
                    
                <input
                      type="text"
                    />
                  </div>
                  <div className="col s6">
                     Cartrust**
                <input
                      type="number"
                      min="0"
                      name="cartrust_total_cost"
                      value={ valueTrack.cartrust_total_cost }
                      onChange={ cartrustTotalCost }
                    />
                  </div>

                </div>
                <div className="col s12 m12 l4  blue lighten-4">
                  <h6> </h6>
                  <div className="col s6">
                    car shield**
                <input
                      type="number"
                      min="0"
                      name="car_shield_fee"
                      value={ valueTrack.car_shield_fee }
                      onChange={ handleChangeTracking }
                    />
                  </div>
                  <div className="col s6">
                    
                <input
                      type="text"
                    />
                  </div>

                  <div className="col s6">
                    **
                <input
                      type="number"
                      min="0"
                      name="car_insurance_fee"
                      value={ valueTrack.car_insurance_fee }
                      onChange={ handleChangeTracking }
                    />
                  </div>
                  <div className="col s6">
                    
                <input
                      type="text"
                    />
                  </div>

                  <div className="col s6">
                    **
                <input
                      type="number"
                      min="0"
                      name="transfer_service_fee"
                      value={ valueTrack.transfer_service_fee }
                      onChange={ handleChangeTracking }
                    />
                  </div>
                  <div className="col s6">
                    
                <input
                      type="text"
                    />
                  </div>

                  <div className="col s6">
                    **
                <input
                      type="number"
                      min="0"
                      name="contract_fee"
                      value={ valueTrack.contract_fee }
                      onChange={ handleChangeTracking }
                    />
                  </div>
                  <div className="col s6">
                    
                <input
                      type="text"
                    />
                  </div>

                  <div className="col s6">
                    **
                <input
                      type="number"
                      min="0"
                      name="outside_transfer_fee"
                      value={ valueTrack.outside_transfer_fee }
                      onChange={ handleChangeTracking }
                    />
                  </div>
                  <div className="col s6">
                    
                <input
                      type="text"
                    />
                  </div>

                  <div className="col s6">
                    **
                <input
                      type="number"
                      min="0"
                      name="tax_renewal_fee"
                      value={ valueTrack.tax_renewal_fee }
                      onChange={ handleChangeTracking }
                    />
                  </div>
                  <div className="col s6">
                    
                <input
                      type="text"
                    />
                  </div>
                  <div className="col s6">
                    **
                <input
                      type="number"
                      min="0"
                      name="act_renewal_fee"
                      value={ valueTrack.act_renewal_fee }
                      onChange={ handleChangeTracking }
                    />
                  </div>
                  <div className="col s6">
                    
                <input
                      type="text"
                    />
                  </div>
                  <div className="col s6">
                    **
                <input
                      type="number"
                      min="0"
                      name="new_finance_total_cost"
                      value={ valueTrack.new_finance_total_cost }
                      onChange={ handleChangeTracking }
                    />
                  </div>
                </div>  {/*finish cartrust */ }

                <div className="col s12 m12 l4  blue lighten-3">
                  <h6 > F2</h6>
                  <div className="col s6">
                    
                <input
                      type="number"
                      min="0"
                    />
                  </div>
                  <div className="col s6">
                    
                <input
                      type="text"
                    />
                  </div>
                  <div className="col s6">
                    
                <input
                      type="text"
                    />
                  </div>
                  <div className="col s6">
                    
                  <input type="text"
                    />
                  </div>
                  <div className="col s6">
                    
                    <input type="text"
                    />
                  </div>
                  <div className="col s6">
                    
                      <input type="text" />
                  </div>
                  <div className="col s6">
                    <button className="col s12 btn green my-3" onClick={ printPDF }>print pdf</button>
                  </div>
                </div>
              </div>
            </>
          ) }

          { step === 8 && (
            <>
              <div className="row">
                <div className="col s12 m6 l3">
                  <div className="file-field input-field">
                    <div className="btn">
                      <span> </span>
                      <input
                        type="file"
                        name='car_license_book_picture'
                        value={ valueTrack.car_license_book_picture }
                        onChange={ handleChangefilePicture }
                      />
                    </div>
                    <div className="file-path-wrapper">
                      <input className="file-path validate" type="text" placeholder="Upload one " />
                    </div>
                  </div>
                </div>
              </div>
            </>
          ) }

          { step === 10 && (
            <>

              <div className="row">
                <div className="col s12 m6 l3">
                  
                  <input
                    type="text"
                    name="close_amount"
                    value={ valueTrack.close_amount }
                  />
                </div>
              </div>
            </>
          ) }

          {/* { step === 13 && (
            <>
              <div className="row">
                <div className="col s12 m6 l3">
                  <p>searh field  :????????</p>

                </div>
              </div>
            </>
          ) } */}

          { step === 14 && (
            <>
              <div className="row">
                <div className="col s12 m6 l3">
                  
                  <input
                    type="number"
                    name="deposit"
                    value={ valueTrack.deposit }
                    onChange={ handleChangeTracking }
                  />
                </div>
              </div>
            </>
          ) }

        </div >
        <div className="modal-footer btnModalView ">
          <button className="btn wave-effect blue  lighten waves-green  " disabled={ step === 0 } onClick={ prevStep }>Prev</button>
          <button className="btn wave-effect green   waves-green  " onClick={ beforSaveTracking } disabled={ step === 0 }>Save Trangking</button>

          <div>
            <button className="btn wave-effect blue lighten  waves-green" style={ { marginRight: '40px' } } disabled={ step === 15 } onClick={ nextStep } >Next</button>
            <a href="#!" className="modal-close waves-effect waves-green btn white black-text">Close</a>
          </div>
        </div>
      </div >
    </div >
  )
}

export default ModalTracking
