import React, { useEffect, useState } from 'react'
import Search from './Search'
import Navbar from './../layout/Navbar';
import ModalDetail from './ModalDetail';
import ModalEdit from './ModalEdit';
import ModalDelete from './ModalDelete';
import ModalCreate from './ModalCreate';
import TableHead from './TableHead';
import axios from "axios"
import url from '../../Utility/url'
import M from 'materialize-css/dist/js/materialize.min.js'

const Users = () => {

  const [users, setUsers] = useState([])
  const [user, setUser] = useState({})

  useEffect(() => {
    M.Modal.init(document.querySelectorAll('.modal'), {})
    getAllUsers()
  }, [])

  const readUser = (u) => setUser(u)

  const deleteUser = (id) => {

    axios.delete(`${url}/delete_user?user_id=${id}`)
      .then((res) => {
        console.log(res.data.message)

        M.toast({ html: `${res.data.message}` })
        getAllUsers()
      })
      .catch(err => { console.log(err) })
  }

  const addUser = (u) => {

    const body = {
      firstname: u.firstname,
      lastname: u.lastname,
      position: u.position,
      team: u.team,
      picture: u.file,
      email: u.email,
      tel: u.phone,
      address: u.address,
      username: u.username,
      password: u.password,
      role_1: u.role_1,
      role_2: u.role_2,
      role_3: u.role_3,
      type: "SE",
      attribute: "attribute_1"
    }


    axios.post(`${url}/register`, body)
      .then(res => {

        M.toast({ html: `${res.data.message}` })
        getAllUsers()
      })
      .catch(err => console.error(err))

  }

  const editUser = (id, body) => {
    console.log(`usersPage`)
  }

  const searchUser = (name) => {
    axios.get(`${url}/search_user?value=${name}&parameter=firstname`)
      .then(res => {
        if (res.data.message.length === 0) {
          M.toast({ html: 'No name this' })
          setUsers([])
        }
        else { setUsers(res.data.message) }
      })
      .catch(err => console.error(err))
  }

  const getAllUsers = () => {
    // setUsers(fackeAccount)
    axios.get(`${url}/user_limit?size=50&page=1`)
      .then(res => {
        setUsers(res.data.message)
        // console.log(res.data.message)

      })
      .catch(err => { console.log(err) })
  }

  return (
    <>
      <Navbar />
      <main>
        <div className="nContainer">
          <h3>All Account : <span className="chip blue">{ users.length }</span></h3>

          <Search searchUser={ searchUser } getAllUsers={ getAllUsers } />

          <a className="waves-effect waves-light btn modal-trigger" style={ { margin: '25px' } } href="#modal4" >create user</a>

          <ul className="pagination">
            <li className="disabled"><a href="#!"><i className="material-icons">chevron_left</i></a></li>
            <li className="active"><a href="#!">1</a></li>
            <li className="waves-effect"><a href="#!">2</a></li>
            <li className="waves-effect"><a href="#!">3</a></li>
            <li className="waves-effect"><a href="#!">4</a></li>
            <li className="waves-effect"><a href="#!">5</a></li>
            <li className="waves-effect"><a href="#!"><i className="material-icons">chevron_right</i></a></li>
          </ul>

          <table className="highlight centered responsive-table ">
            <TableHead />
            <tbody>
              { users.map((item, index) => (
                <tr key={ `${item.user_id ? item.user_id : index}` }>
                  <td>{ item.id }</td>
                  <td> { item.picture
                    ? <img src={ item.picture } alt="img.profile" className="circle responsive-img" style={ { width: "35px" } } />
                    : <h6>{ item.firstnamestr.charAt(0) }</h6> }
                  </td>
                  <td>{ item.firstname }</td>
                  <td>{ item.lastname }</td>
                  <td>{ item.team_name }</td>
                  <td>{ item.position }</td>
                  <td>{ item.authority }</td>
                  <td>{ item.tel }</td>
                  <td>{ item.email }</td>
                  <td>

                    <a className="waves-effect waves-light btn blue modal-trigger m-1" href="#modal1" onClick={ () => readUser(item) }>detail</a>

                    <a className="waves-effect waves-light btn yellow black-text m-1 modal-trigger" href="#modal2" onClick={ () => readUser(item) }>edit</a>

                    <a className="waves-effect waves-light btn pink m-1 modal-trigger" href="#modal3" onClick={ () => readUser(item) }>delete</a>

                  </td>
                </tr>
              )) }
            </tbody>
          </table>
        </div>

        <ModalDetail user={ user } />

        <ModalEdit user={ user } editUser={ editUser } />

        <ModalDelete user={ user } deleteUser={ deleteUser } />

        <ModalCreate addUser={ addUser } />

      </main>
    </>
  )
}

export default Users
